import './index.scss';

import Icon from 'components/ui/Icon';

import classNames from 'classnames';
import { ButtonHTMLAttributes, ReactNode } from 'react';

// ----------------------------------------------------------------------

export type ButtonProps = {
  flex?: 'none' | 1 | 2 | 3;
  grow?: 'none' | 1 | 2 | 3;
  variant?: 'default' | 'ghost' | 'link' | 'outline';
  character?: 'default' | 'modal' | 'inline' | 'table' | 'form' | 'cta';
  color?: 'primary' | 'secondary';
  full?: boolean;
  loaded?: boolean;
  prefix?: ReactNode;
  suffix?: ReactNode;
  animated?: boolean;
  className?: string;
} & ButtonHTMLAttributes<HTMLButtonElement>;

export interface CtaButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {}

const CHARACTER_MAPS = {
  default: 'qb-button--default',
  modal: 'qb-button--modal',
  inline: 'qb-button--inline',
  table: 'qb-button--table',
  form: 'qb-button--form',
  cta: 'qb-button--cta'
};

const COLOR_MAPS = {
  primary: 'qb-button--primary',
  secondary: 'qb-button--secondary'
};

const FLEX_BASIS_MAPS = {
  none: '',
  1: 'qb-button--flex-1',
  2: 'qb-button--flex-2',
  3: 'qb-button--flex-3'
};

const FLEX_GROW_MAPS = {
  none: '',
  1: 'qb-button--grow-1',
  2: 'qb-button--grow-2',
  3: 'qb-button--grow-3'
};

export const CTAButton = ({ type = 'button', onClick }: CtaButtonProps) => {
  return (
    <button type={type} className="qb-button qb-button--cta" onClick={onClick}>
      <Icon name="icon-asset-plus" size="text-40" color="text-primary-500" />
    </button>
  );
};

const Button = ({
  type = 'button',
  children,
  disabled,
  grow = 'none',
  flex = 'none',
  variant = 'default',
  character = 'default',
  color = 'primary',
  full = false,
  loaded = true,
  prefix,
  suffix,
  animated = false,
  className,
  onClick
}: ButtonProps) => {
  const VARIANT_BUTTON = (variant: string) => {
    switch (variant) {
      case 'ghost':
        return 'qb-button--ghost';
      case 'link':
        return 'qb-button--link';
      case 'outline':
        return `qb-button--outline ${CHARACTER_MAPS[character]} ${COLOR_MAPS[color]}`;
      case 'default':
      default:
        return `${CHARACTER_MAPS[character]} ${COLOR_MAPS[color]}`;
    }
  };

  return (
    <>
      {character === 'cta' ? (
        <CTAButton type={type} onClick={onClick} />
      ) : (
        <button
          type={type}
          className={classNames(
            'qb-button',
            FLEX_GROW_MAPS[grow],
            FLEX_BASIS_MAPS[flex],
            VARIANT_BUTTON(variant),

            {
              'qb-button--full-width': full,
              'qb-button--disabled': disabled,
              'qb-button--no-animation': !animated
            },
            className
          )}
          disabled={disabled}
          onClick={onClick}
        >
          {!loaded && <span className="loading loading-spinner" />}
          {prefix}
          {children}
          {suffix}
        </button>
      )}
    </>
  );
};

export default Button;
