import './index.scss';

import classNames from 'classnames';
import ErrorMessage from 'components/forms/ErrorMessage';
import Label from 'components/forms/Label';
import Icon from 'components/ui/Icon';
import Typography from 'components/ui/Typography';
import useClickOutside from 'hooks/useClickOutside';
import _get from 'lodash/get';
import { KeyboardEvent, useCallback, useEffect, useState } from 'react';
import { Controller, useFormContext } from 'react-hook-form';

// ----------------------------------------------------------------------

export interface OptionProps {
  id: number;
  label: string;
  value: string | number | boolean;
}

export type SelectProps = {
  name: string;
  placeholder?: string;
  label?: string;
  required?: boolean;
  flexDirection?: 'row' | 'column';
  variant?: 'title' | 'item' | 'table';
  list: OptionProps[];
  defaultValue?: OptionProps['value'];
  disabled?: boolean;
  readonly?: boolean;
  isEllipsis?: boolean;
  maxRow?: 3 | 4 | 5 | 6 | 7;
  resetThenSet?: (id: number) => void;
  onChange?: (option: OptionProps) => void;
  hasContext?: boolean;
};

const VARIANT_MAPS = {
  title: 'qb-select--title',
  item: 'qb-select--item',
  table: 'qb-select--table'
};

const MAX_ROW_MAPS = {
  3: `max-h-[8.8125rem]`,
  4: `max-h-[11.375rem]`,
  5: 'max-h-[13.9375rem]',
  6: 'max-h-[16.5rem]',
  7: 'max-h-[19.0625rem]'
};

export const ContextSelect = ({
  name,
  placeholder,
  resetThenSet,
  onChange,
  flexDirection = 'column',
  variant = 'item',
  label,
  required,
  list,
  defaultValue,
  disabled = false,
  readonly = false,
  isEllipsis = true,
  maxRow = 5
}: SelectProps) => {
  const { control, setValue, formState } = useFormContext();
  const errorMessage = _get(formState?.errors, `${name}.message`, '') as string;

  const [isListOpen, setIsListOpen] = useState<boolean>(false);
  const [selectedLabel, setSelectedLabel] = useState<string | undefined>(undefined);
  const [selectedIndex, setSelectedIndex] = useState<number>(0);

  const toggleList = useCallback(() => {
    setIsListOpen((prev) => !prev);
  }, []);

  const selectItem = useCallback(
    ({ id, label, value }: OptionProps) => {
      setSelectedLabel(label);
      setSelectedIndex(value as number);
      setValue(name, value, { shouldValidate: true });
      setIsListOpen(false);
      if (resetThenSet) {
        resetThenSet(id);
      }
      if (onChange) {
        onChange({ id, label, value });
      }
    },
    [name, onChange, resetThenSet, setValue]
  );

  const handleArrowKeys = useCallback(
    (e: KeyboardEvent<HTMLElement>) => {
      switch (e.key) {
        case 'Tab':
          if (e.shiftKey) {
            e.preventDefault();
            const newIndex = selectedIndex > 0 ? selectedIndex - 1 : list.length - 1;
            setSelectedIndex(newIndex);
            setSelectedLabel(list[newIndex].label);
            break;
          } else {
            e.preventDefault();
            const newIndex = selectedIndex < list.length - 1 ? selectedIndex + 1 : 0;
            setSelectedIndex(newIndex);
            setSelectedLabel(list[newIndex].label);
            break;
          }
        case 'ArrowDown': {
          e.preventDefault();
          const newIndex = selectedIndex < list.length - 1 ? selectedIndex + 1 : 0;
          setSelectedIndex(newIndex);
          setSelectedLabel(list[newIndex].label);
          break;
        }
        case 'ArrowUp': {
          e.preventDefault();
          const newIndex = selectedIndex > 0 ? selectedIndex - 1 : list.length - 1;
          setSelectedIndex(newIndex);
          setSelectedLabel(list[newIndex].label);
          break;
        }
        case 'Enter': {
          e.preventDefault();
          const selectedItem = list.find((item) => item.label === selectedLabel);
          if (selectedItem) {
            selectItem(selectedItem);
          }
          break;
        }
        default:
          null;
      }
    },
    [list, selectItem, selectedIndex, selectedLabel]
  );

  const listRef = useClickOutside(() => {
    setIsListOpen(false);
  });

  useEffect(() => {
    const defaultItem = list.find((item) => item.value === defaultValue);
    if (defaultItem) {
      setSelectedLabel(defaultItem.label);
    }
  }, [defaultValue, list]);

  return (
    <Controller
      name={name}
      control={control}
      render={({ field: { ref, ...fieldRest } }) => {
        return (
          <div
            className={classNames(
              'qb-select',
              {
                'qb-select--row': flexDirection === 'row'
              },
              VARIANT_MAPS[variant]
            )}
          >
            {label && <Label text={label} required={required} />}
            <div className="qb-select__header">
              <button
                type="button"
                className={classNames(
                  'qb-select__pressable focus-visible--outline',
                  {
                    'pointer-events-none': isListOpen
                  },
                  {
                    'qb-select__pressable--disabled': disabled
                  },
                  {
                    'qb-select__pressable--readonly': readonly
                  },
                  {
                    'qb-select__pressable--error': errorMessage
                  }
                )}
                onClick={disabled || readonly ? (e) => e.preventDefault() : toggleList}
              >
                {isListOpen ? (
                  <Icon
                    name="icon-chevron-up-large"
                    size="text-24"
                    color="text-gray-500"
                    className="qb-select__toggle-icon"
                  />
                ) : (
                  <Icon
                    name="icon-chevron-down-large"
                    size="text-24"
                    color="text-gray-500"
                    className="qb-select__toggle-icon"
                  />
                )}
              </button>
              <input
                type="text"
                ref={ref}
                placeholder={placeholder}
                disabled
                {...fieldRest}
                value={selectedLabel || ''}
                className={classNames(
                  'qb-select__value',
                  {
                    'qb-select__value--active': isListOpen
                  },
                  {
                    'qb-select__value--ellipsis': isEllipsis
                  }
                )}
              />
              {errorMessage && <ErrorMessage text={errorMessage} />}
            </div>
            {isListOpen && (
              <div ref={listRef} className="qb-select__list-wrapper">
                <ul role="list" className={classNames('qb-select__list', MAX_ROW_MAPS[maxRow])}>
                  {list.map((item) => (
                    <li
                      key={item.id}
                      className={classNames('qb-select__item', {
                        'qb-select__item--selected': item.label === selectedLabel
                      })}
                    >
                      <button
                        type="button"
                        className="qb-select__item-pressable"
                        onClick={() => selectItem(item)}
                        onKeyDown={(e) => handleArrowKeys(e)}
                      >
                        <Typography className="qb-select__item-label">{item.label}</Typography>
                      </button>
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        );
      }}
    />
  );
};

const Select = ({
  name,
  placeholder = '',
  label,
  required,
  flexDirection = 'column',
  variant = 'item',
  list,
  defaultValue,
  disabled = false,
  readonly = false,
  isEllipsis = true,
  maxRow = 5,
  resetThenSet,
  onChange,
  hasContext = true
}: SelectProps) => {
  const [isListOpen, setIsListOpen] = useState<boolean>(false);
  const [selectedLabel, setSelectedLabel] = useState<string | undefined>(undefined);
  const [selectedIndex, setSelectedIndex] = useState<number>(0);

  const toggleList = useCallback(() => {
    setIsListOpen((prev) => !prev);
  }, []);

  const selectItem = useCallback(
    ({ id, label, value }: OptionProps) => {
      setSelectedLabel(label);
      setIsListOpen(false);
      if (resetThenSet) {
        resetThenSet(id);
      }
      if (onChange) {
        onChange({ id, label, value });
      }
    },
    [onChange, resetThenSet]
  );

  const handleArrowKeys = useCallback(
    (e: KeyboardEvent<HTMLElement>) => {
      switch (e.key) {
        case 'Tab':
          if (e.shiftKey) {
            e.preventDefault();
            const newIndex = selectedIndex > 0 ? selectedIndex - 1 : list.length - 1;
            setSelectedIndex(newIndex);
            setSelectedLabel(list[newIndex].label);
            break;
          } else {
            e.preventDefault();
            const newIndex = selectedIndex < list.length - 1 ? selectedIndex + 1 : 0;
            setSelectedIndex(newIndex);
            setSelectedLabel(list[newIndex].label);
            break;
          }
        case 'ArrowDown': {
          e.preventDefault();
          const newIndex = selectedIndex < list.length - 1 ? selectedIndex + 1 : 0;
          setSelectedIndex(newIndex);
          setSelectedLabel(list[newIndex].label);
          break;
        }
        case 'ArrowUp': {
          e.preventDefault();
          const newIndex = selectedIndex > 0 ? selectedIndex - 1 : list.length - 1;
          setSelectedIndex(newIndex);
          setSelectedLabel(list[newIndex].label);
          break;
        }
        case 'Enter': {
          e.preventDefault();
          const selectedItem = list.find((item) => item.label === selectedLabel);
          if (selectedItem) {
            selectItem(selectedItem);
          }
          break;
        }
        default:
          null;
      }
    },
    [list, selectItem, selectedIndex, selectedLabel]
  );

  const listRef = useClickOutside(() => {
    setIsListOpen(false);
  });

  useEffect(() => {
    const defaultItem = list.find((item) => item.value === defaultValue);
    if (defaultItem) {
      setSelectedLabel(defaultItem.label);
    }
  }, [defaultValue, list]);

  return (
    <>
      {hasContext ? (
        <ContextSelect
          name={name}
          placeholder={placeholder}
          onChange={onChange}
          flexDirection={flexDirection}
          variant={variant}
          label={label}
          required={required}
          disabled={disabled}
          readonly={readonly}
          list={list}
          isEllipsis={isEllipsis}
          maxRow={maxRow}
          defaultValue={defaultValue}
          resetThenSet={resetThenSet}
        />
      ) : (
        <div
          className={classNames(
            'qb-select',
            {
              'qb-select--row': flexDirection === 'row'
            },
            VARIANT_MAPS[variant]
          )}
        >
          {label && <Label text={label} required={required} />}
          <div className="qb-select__header">
            <button
              type="button"
              className={classNames(
                'qb-select__pressable focus-visible--outline',
                {
                  'pointer-events-none': isListOpen
                },
                {
                  'qb-select__pressable--disabled': disabled
                },
                {
                  'qb-select__pressable--readonly': readonly
                }
              )}
              onClick={disabled || readonly ? (e) => e.preventDefault() : toggleList}
            >
              {isListOpen ? (
                <Icon
                  name="icon-chevron-up-small"
                  size="text-24"
                  color="text-gray-500"
                  className="qb-select__toggle-icon"
                />
              ) : (
                <Icon
                  name="icon-chevron-down-small"
                  size="text-24"
                  color="text-gray-500"
                  className="qb-select__toggle-icon"
                />
              )}
            </button>
            <input
              type="text"
              placeholder={placeholder}
              disabled
              value={selectedLabel || ''}
              className={classNames(
                'qb-select__value',
                {
                  'qb-select__value--active': isListOpen
                },
                {
                  'qb-select__value--ellipsis': isEllipsis
                }
              )}
            />
          </div>
          {isListOpen && (
            <div ref={listRef} className="qb-select__list-wrapper">
              <ul role="list" className={classNames('qb-select__list', MAX_ROW_MAPS[maxRow])}>
                {list.map((item) => (
                  <li
                    key={item.id}
                    className={classNames('qb-select__item', {
                      'qb-select__item--selected': item.label === selectedLabel
                    })}
                  >
                    <button
                      type="button"
                      className="qb-select__item-pressable"
                      onClick={() => selectItem(item)}
                      onKeyDown={(item) => handleArrowKeys(item)}
                    >
                      <Typography className="qb-select__item-label">{item.label}</Typography>
                    </button>
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>
      )}
    </>
  );
};

export default Select;
