import { ReactNode } from 'react';

import Icon from '../Icon';

import classNames from 'classnames';
import './index.scss';

// ---------------------------------------------------------------------- //

export type CardProps = {
  children: ReactNode;
  backgroundColor?: string;
  alignItems?: 'start' | 'center' | 'end';
  hasCloseButton?: boolean;
  hasShadow?: boolean;
  onClose?: () => void;
  onClick?: () => void;
  gap?: 4 | 8 | 12 | 16 | 20 | 24;
  py?: string;
  pt?: string;
  pb?: string;
  px?: string;
  className?: string;
};

const ALIGN_MAPS = {
  start: 'qb-card--items-start',
  center: 'qb-card--items-center',
  end: 'qb-card--items-end'
};

const GAP_MAPS = {
  4: 'gap-1',
  8: 'gap-2',
  12: 'gap-3',
  16: 'gap-4',
  20: 'gap-5',
  24: 'gap-6'
};

const Card = ({
  children,
  backgroundColor,
  alignItems = 'start',
  hasCloseButton = false,
  hasShadow = false,
  onClose,
  onClick,
  gap,
  pt = 'pt-10',
  pb = 'pb-[3.125rem]',
  py,
  px = 'px-[3.125rem]',
  className
}: CardProps) => {
  const CustomTag = onClick ? 'button' : 'div';

  return (
    <CustomTag
      {...(onClick && { type: 'button' })}
      className={classNames(
        'qb-card',
        { 'qb-card--closable': hasCloseButton },
        { 'qb-card--touchable': onClick },
        { 'qb-card--shadow': hasShadow },
        backgroundColor ? backgroundColor : 'qb-card--white',
        ALIGN_MAPS[alignItems],
        gap ? GAP_MAPS[gap] : undefined,
        !py && pt ? pt : '',
        !py && pb ? pb : '',
        py,
        px,
        className
      )}
    >
      {hasCloseButton && (
        <div className="qb-card__close-area">
          <Icon name="icon-basic-close" onClick={onClose} className="qb-card__close-icon" />
        </div>
      )}
      {children}
    </CustomTag>
  );
};

export default Card;
