import './index.scss';

import classNames from 'classnames';
import { Typography } from 'components/ui';
import { motion } from 'framer-motion';
import { ReactNode, useCallback } from 'react';

// ----------------------------------------------------------------------

type TabsList = {
  no: number | string;
  label: ReactNode;
};
export type TabsProps = {
  currentIndex?: number;
  list: TabsList[];
  onChange: (payload: { data: TabsList; index: number }) => void;
  variant?: 'default' | 'box';
  color?: 'primary' | 'secondary';
  hasAnimated?: boolean;
};

const VARIANT_MAPS = {
  default: '',
  box: 'qb-tabs--box'
};

const UNDERLINE_MAPS = {
  primary: 'qb-tabs__item-underline--primary',
  secondary: 'qb-tabs__item-underline--secondary'
};

const Tabs = ({
  list,
  currentIndex,
  onChange,
  variant = 'default',
  color = 'primary',
  hasAnimated = false
}: TabsProps) => {
  const onClickTab = useCallback(
    (index: number) => {
      onChange({ data: list[index], index });
    },
    [list, onChange]
  );
  return (
    <div className={classNames('qb-tabs', VARIANT_MAPS[variant])}>
      <ul className="qb-tabs__list">
        {list.map(({ no, label }, index) => (
          <li key={no} className="qb-tabs__item">
            <button
              type="button"
              className="qb-tabs__item-pressable"
              onClick={() => {
                onClickTab(index);
              }}
            >
              <Typography
                size="lg"
                className={classNames('qb-tabs__item-label', {
                  'qb-tabs__item-label--active': currentIndex === index
                })}
              >
                {label}
              </Typography>
              {index === currentIndex ? (
                <>
                  {hasAnimated ? (
                    <motion.div
                      className={classNames('qb-tabs__item-underline', UNDERLINE_MAPS[color])}
                      layoutId="underline"
                    />
                  ) : (
                    <div className={classNames('qb-tabs__item-underline', UNDERLINE_MAPS[color])} />
                  )}
                </>
              ) : null}
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Tabs;
