import classNames from 'classnames';
import { Touchable } from 'components/ui';

// ----------------------------------------------------------------------

export type IconProps = {
  name: IconTypes;
  size?: string;
  color?: string;
  onClick?: () => void;
  className?: string;
};

const Icon = ({
  name,
  size = 'text-20',
  color = 'text-gray-600',
  onClick,
  className
}: IconProps) => {
  return (
    <>
      {onClick ? (
        <Touchable onClick={onClick} className={className}>
          <i
            className={classNames(
              'inline-flex items-center justify-center shrink-0',
              name,
              size,
              color,
              className
            )}
          />
        </Touchable>
      ) : (
        <i
          className={classNames(
            'inline-flex items-center justify-center shrink-0',
            name,
            size,
            color,
            className
          )}
        />
      )}
    </>
  );
};

export default Icon;
