import { ReactNode, useId } from 'react';
import ReactModal from 'react-modal';

import Typography from 'components/ui/Typography';
import ButtonGroup from 'components/ui/ButtonGroup';
import Button from 'components/ui/Button';
import Icon from 'components/ui/Icon';

import classNames from 'classnames';
import './index.scss';
// ----------------------------------------------------------------------

export type ConfirmProps = {
  isOpen: boolean;
  contentLabel?: string;
  titleAlign?: 'left' | 'center';
  title?: ReactNode;
  children: ReactNode;
  onAfterOpen?: () => void;
  onAfterClose?: () => void;
  actionsAlign?: 'center' | 'right';
  onClose?: () => void;
  closeLabel?: string;
  onSuccess?: () => void;
  successLabel?: string;
  shouldCloseOnOverlayClick?: boolean;
  onRequestClose?: () => void;
  hasCustomActions?: boolean;
  hasCloseButton?: boolean;
};

const TITLE_POSITION_MAPS = {
  left: 'qb-confirm__header--left',
  center: 'qb-confirm__header--center'
};

const ACTIONS_POSITION_MAPS = {
  center: 'qb-confirm__actions--center',
  right: 'qb-confirm__actions--right'
};

const Confirm = ({
  isOpen = false,
  contentLabel,
  titleAlign = 'left',
  title,
  children,
  onAfterOpen,
  onAfterClose,
  actionsAlign = 'center',
  onClose,
  closeLabel = '',
  onSuccess,
  successLabel = '',
  shouldCloseOnOverlayClick = true,
  onRequestClose,
  hasCustomActions = false,
  hasCloseButton = false
}: ConfirmProps) => {
  const id = useId();

  return (
    <ReactModal
      id={`confirm${id}`}
      role="confirm"
      isOpen={isOpen}
      contentLabel={contentLabel || 'Confirm'}
      portalClassName="qb-confirm"
      ariaHideApp={false}
      onAfterOpen={onAfterOpen}
      onAfterClose={onAfterClose}
      onRequestClose={onRequestClose}
      shouldCloseOnOverlayClick={shouldCloseOnOverlayClick}
      overlayClassName="qb-confirm__overlay"
      className="qb-confirm__container"
      contentElement={(props) => (
        <div className="qb-confirm__content" {...props}>
          {title && (
            <div
              className={classNames('qb-confirm__header', TITLE_POSITION_MAPS[titleAlign], {
                'qb-confirm__header--closable': onClose
              })}
            >
              <Typography size="lg" weight={700} className="qb-confirm__header-title">
                {title}
              </Typography>
              {hasCloseButton && (
                <div className="qb-confirm__header-closed-icon">
                  <Icon name="icon-basic-close" size="text-20" onClick={onClose} />
                </div>
              )}
            </div>
          )}
          <div className="qb-confirm__body">{children}</div>
          {!hasCustomActions && (
            <div className={classNames('qb-confirm__actions', ACTIONS_POSITION_MAPS[actionsAlign])}>
              <ButtonGroup>
                {onClose && (
                  <Button flex={1} character="modal" color="secondary" onClick={onClose}>
                    {closeLabel}
                  </Button>
                )}
                <Button flex={1} character="modal" onClick={onSuccess}>
                  {successLabel}
                </Button>
              </ButtonGroup>
            </div>
          )}
        </div>
      )}
    />
  );
};

export default Confirm;
