import './index.scss';

import classNames from 'classnames';
import Typography from 'components/ui/Typography';

// ----------------------------------------------------------------------

export type LabelProps = {
  width?: string;
  text: string;
  required?: boolean;
  color?: 'default' | 'primary' | 'secondary';
};

const COLOR_MAPS = {
  default: '',
  primary: 'qb-label--primary',
  secondary: 'qb-label--secondary'
};

const Label = ({ width, text, required = false, color = 'default' }: LabelProps) => {
  return (
    <Typography weight={700} className={classNames('qb-label', width)} color={COLOR_MAPS[color]}>
      {text}
      {required && <i className="qb-label__required-icon">*</i>}
    </Typography>
  );
};

export default Label;
