import './index.scss';

import classNames from 'classnames';
import { PropsWithChildren } from 'react';

// ----------------------------------------------------------------------

export interface ButtonGroupProps extends PropsWithChildren {
  flexDirection?: 'row' | 'column';
  className?: string;
}

const ButtonGroup = ({ children, flexDirection = 'row', className = '' }: ButtonGroupProps) => {
  return (
    <div
      className={classNames(
        'button-group',
        flexDirection === 'row' ? 'button-group--row' : 'button-group--column',
        className
      )}
    >
      {children}
    </div>
  );
};

export default ButtonGroup;
