import { ReactNode } from 'react';

import classNames from 'classnames';

// ----------------------------------------------------------------------

export type ChipProps = {
  variant?: 'filled' | 'outlined' | 'ghost';
  size?: 'default' | 'lg' | 'sm';
  color?: 'default' | 'blue' | 'red' | 'yellow' | 'green';
  label: ReactNode;
  prefix?: ReactNode;
  suffix?: ReactNode;
  onClick?: () => void;
  disabled?: boolean;
};

const SIZE_MAPS = {
  default: 'py-1 px-2 rounded-form-md',
  lg: 'py-1 px-3 rounded-form-md',
  sm: 'px-1 rounded-form-sm'
};

const FONT_MAPS = {
  default: 'text-body-xs font-700',
  lg: 'text-body-sm font-700',
  sm: 'text-caption'
};

const BACKGROUND_MAPS = {
  default: 'bg-gray-600/10',
  blue: 'bg-primary-500/10',
  red: 'bg-red-default/10',
  yellow: 'bg-yellow-default/10',
  green: 'bg-green-default/10'
};

const BORDER_MAPS = {
  default: 'bg-gray-600/12',
  blue: 'border-blue-default/12',
  red: 'border-red-default/12',
  yellow: 'border-yellow-default/12',
  green: 'border-green-default/12'
};

const COLOR_MAPS = {
  default: 'text-gray-600',
  blue: 'text-primary-500',
  red: 'text-red-default',
  yellow: 'text-yellow-default',
  green: 'text-green-default'
};

const Chip = ({
  variant = 'filled',
  size = 'default',
  color = 'default',
  label,
  prefix,
  suffix,
  onClick,
  disabled = false
}: ChipProps) => {
  const CustomTag = onClick ? 'button' : 'div';

  return (
    <CustomTag
      {...(onClick && { type: 'button', disabled })}
      className={classNames(
        'qb-chip inline-flex items-center gap-0.5 border',
        SIZE_MAPS[size],
        variant === 'filled' && !disabled ? BACKGROUND_MAPS[color] : 'bg-transparent',
        variant !== 'ghost' && !disabled ? BORDER_MAPS[color] : 'border-transparent',
        { 'opacity-20 cursor-not-allowed': disabled }
      )}
      onClick={onClick}
    >
      {prefix}
      <span className={classNames(FONT_MAPS[size], COLOR_MAPS[color])}>{label}</span>
      {suffix}
    </CustomTag>
  );
};

export default Chip;
