import './index.scss';

import classNames from 'classnames';
import { HTMLAttributes } from 'react';

// ----------------------------------------------------------------------

export type DividerProps = {
  my?: 4 | 8 | 12 | 16 | 20 | 24 | 28 | 32;
  type?: 'solid' | 'dotted' | 'dashed';
  color?: string;
} & HTMLAttributes<HTMLDivElement>;

const SPACING_MAPS = {
  4: 'my-1',
  8: 'my-2',
  12: 'my-3',
  16: 'my-4',
  20: 'my-5',
  24: 'my-6',
  28: 'my-7',
  32: 'my-8'
};

const TYPE_MAPS = {
  solid: 'qb-divider--solid',
  dotted: 'qb-divider--dotted',
  dashed: 'qb-divider--dashed'
};

const Divider = ({ my = 20, type = 'solid', color = 'border-gray-400' }: DividerProps) => {
  return (
    <div
      className={classNames(
        'qb-divider',
        SPACING_MAPS[my],
        TYPE_MAPS[type],
        color ? color : 'qb-divider--white'
      )}
    />
  );
};

export default Divider;
