import { HTMLAttributes } from 'react';

import classNames from 'classnames';
import './index.scss';
import { OptionProps } from 'components/forms/Select';
import _map from 'lodash/map';

export type ListTableProps = {
  direction?: 'col' | 'row';
  useLevel?: boolean;
  useLevelColor?: boolean;
  data: OptionProps[];
} & HTMLAttributes<HTMLDivElement>;

const VALUE_COLOR_MAPS = {
  none: 'text-gray-700',
  high: 'text-level-dark-extreme-high',
  low: 'text-level-dark-extreme-low'
};

const ListTable = ({
  direction = 'col',
  useLevel = false,
  useLevelColor = false,
  data = [],
  className
}: ListTableProps) => {
  return (
    <>
      {data.length > 0 && (
        <div className={classNames(`list-table list-table--${direction}`, className)}>
          <table>
            {direction == 'row' && (
              <thead>
                <tr>
                  {_map(data, (item) => (
                    <th key={item.id}>{item.label}</th>
                  ))}
                </tr>
              </thead>
            )}
            <tbody>
              {direction == 'row' ? (
                <tr>
                  {_map(data, (item) => (
                    <td key={item.id}>
                      <span
                        className={classNames(
                          useLevelColor
                            ? VALUE_COLOR_MAPS[
                                (item.value as number) > 0
                                  ? 'high'
                                  : (item.value as number) == 0
                                  ? 'none'
                                  : 'low'
                              ]
                            : ''
                        )}
                      >
                        {item.value}
                        {useLevel && '%'}
                      </span>
                    </td>
                  ))}
                </tr>
              ) : (
                <>
                  {_map(data, (item) => (
                    <tr key={item.id}>
                      <td>
                        <label>{item.label}</label>
                        <span
                          className={classNames(
                            useLevelColor
                              ? VALUE_COLOR_MAPS[
                                  (item.value as number) > 0
                                    ? 'high'
                                    : (item.value as number) == 0
                                    ? 'none'
                                    : 'low'
                                ]
                              : ''
                          )}
                        >
                          {item.value}
                          {useLevel && '%'}
                        </span>
                      </td>
                    </tr>
                  ))}
                </>
              )}
            </tbody>
          </table>
        </div>
      )}
    </>
  );
};

export default ListTable;
