import './index.scss';

import { ButtonGroup, Icon, Typography } from 'components/ui';
import { AnimatePresence, motion } from 'framer-motion';
import { ReactNode } from 'react';
import ReactModal from 'react-modal';

// ----------------------------------------------------------------------

export type FullSizeModalProps = {
  isOpen: boolean;
  contentLabel?: string;
  header?: boolean;
  title?: string;
  children: ReactNode;
  actions?: ReactNode;
  onAfterOpen?: () => void;
  onAfterClose?: () => void;
  onClose?: () => void;
};

const customStyle = {
  content: {
    inset: 0,
    padding: 0,
    border: '0 none',
    borderRadius: 0
  }
};

const FullSizeModal = ({
  isOpen = false,
  contentLabel,
  header = true,
  title,
  children,
  actions,
  onAfterOpen,
  onAfterClose,
  onClose
}: FullSizeModalProps) => {
  return (
    <AnimatePresence>
      {isOpen && (
        <ReactModal
          isOpen={isOpen}
          contentLabel={contentLabel}
          style={customStyle}
          overlayClassName="qb-modal__overlay"
          portalClassName="qb-modal"
          ariaHideApp={false}
          onAfterOpen={onAfterOpen}
          onAfterClose={onAfterClose}
        >
          <motion.div className="qb-modal__container">
            {header && (
              <div className="qb-modal__header">
                {title && (
                  <div className="qb-modal__header-title-area">
                    <Typography variant="head" className="qb-modal__header-title">
                      {title}
                    </Typography>
                  </div>
                )}
                <div className="qb-modal__closable">
                  <Icon
                    name="icon-basic-close"
                    onClick={onClose}
                    className="qb-modal__closable-icon"
                  />
                </div>
              </div>
            )}
            <div className="qb-modal__body scrollbar-custom">{children}</div>
            {actions && (
              <div className="qb-modal__actions">
                <ButtonGroup>{actions}</ButtonGroup>
              </div>
            )}
          </motion.div>
        </ReactModal>
      )}
    </AnimatePresence>
  );
};

export default FullSizeModal;
