import { HTMLAttributes } from 'react';

import './index.scss';
import { AnimatePresence, motion } from 'framer-motion';
import ReactModal from 'react-modal';
import { fadeAnimation } from 'components/animate/variants/fade';
import Icon from '../Icon';

export type SideModalProps = {
  isOpen: boolean;
  onClose?: () => void;
} & HTMLAttributes<HTMLDivElement>;

const SideModal = ({ isOpen = true, onClose, children }: SideModalProps) => {
  return (
    <AnimatePresence>
      {isOpen && (
        <ReactModal
          isOpen={isOpen}
          portalClassName="qb-side-modal"
          ariaHideApp={false}
          overlayElement={() => (
            <motion.div className="qb-side-modal__overlay" {...fadeAnimation}>
              <motion.div className="qb-side-modal__container scrollbar-custom" {...fadeAnimation}>
                <div className="qb-side-modal__closable">
                  <Icon
                    name="icon-basic-close"
                    onClick={onClose}
                    className="qb-side-modal__closable-icon"
                  />
                </div>
                {children}
              </motion.div>
            </motion.div>
          )}
        />
      )}
    </AnimatePresence>
  );
};

export default SideModal;
