import { useMemo } from 'react';
import {
  ColumnDef,
  flexRender,
  getCoreRowModel,
  getFilteredRowModel,
  getPaginationRowModel,
  getSortedRowModel,
  RowSelectionState,
  SortingState,
  Updater,
  useReactTable
} from '@tanstack/react-table';
import ReactPaginate, { ReactPaginateProps } from 'react-paginate';

import Select from 'components/forms/Select';
import Icon from 'components/ui/Icon';
import Typography from 'components/ui/Typography';
import EmptyView from 'components/ui/EmptyView';

import type { OptionProps } from 'components/forms/Select';
import type { Pagination } from '@package/shared-consts';

import classNames from 'classnames';
import './index.scss';
// ----------------------------------------------------------------------

interface DataTableProps<T> {
  data: Array<T> | undefined;
  isLoading: boolean;
  uniqueKey?: keyof T;
  enableSorting?: boolean; // default: false
  enableRowSelection?: boolean; // default: false
  rowSelection?: RowSelectionState;
  sorting?: SortingState;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  columns: ColumnDef<T, any>[];
  // eslint-disable-next-line @typescript-eslint/no-redundant-type-constituents
  pageMetadata: Pagination.Metadata | undefined;
  enablePagination?: boolean; // default: true
  paginationSettings?: Partial<ReactPaginateProps>;
  onPageChange?: (selectedItem: { selected: number }) => void;
  onPerPageChange?: (perPage: number) => void;
  onSortingChange?: (onChangeFn: Updater<SortingState>) => void;
  onRowSelectionChange?: (onChangeFn: Updater<RowSelectionState>) => void;
  onClickFirstPage?: (selectedItem: { selected: number }) => void;
  onClickLastPage?: (selectedItem: { selected: number }) => void;
}
// ----------------------------------------------------------------------
const initialPaginationSettings: Partial<ReactPaginateProps> = {
  containerClassName: 'qb-pagination__container',
  pageClassName: 'qb-pagination__page-button',
  pageLinkClassName: 'qb-pagination__page-label',
  activeClassName: 'qb-pagination__page-button--active',
  activeLinkClassName: 'qb-pagination__page-label--active',
  // breakLabel: '...',
  // breakClassName: 'qb-pagination__break',
  previousClassName: 'qb-pagination__arrow-button qb-pagination__arrow-button--prev',
  nextClassName: 'qb-pagination__arrow-button qb-pagination__arrow-button--next',
  // nextLabel: <Icon name="icon-chevron-right-small" className="qb-pagination__arrow-icon" />,
  // previousLabel: <Icon name="icon-chevron-left-small" className="qb-pagination__arrow-icon" />,
  disabledClassName: 'qb-pagination__arrow-button--disabled',
  disabledLinkClassName: 'qb-pagination__arrow-icon--disabled',
  disableInitialCallback: true
};
const initialMetadata: Pagination.Metadata = {
  total: 0,
  lastPage: 0,
  currentPage: 0,
  perPage: 10,
  prev: null,
  next: null
};
// ----------------------------------------------------------------------

const { DEV } = import.meta.env;
const noop = () => {};
const PagePerList: OptionProps[] = [
  { id: 1, label: '10', value: 10 },
  { id: 2, label: '15', value: 15 },
  { id: 3, label: '20', value: 20 },
  { id: 4, label: '30', value: 30 }
];

// NOTE USE TanStack Table(https://tanstack.com/)
function DataTable<T>(props: DataTableProps<T>) {
  const {
    data = [],
    columns,
    isLoading,
    rowSelection = {},
    sorting = [],
    enablePagination = true,
    enableSorting = false,
    enableRowSelection = false,
    uniqueKey = 'id' as keyof T,
    paginationSettings = {},
    pageMetadata = initialMetadata,
    onPageChange = noop,
    onPerPageChange = noop,
    onSortingChange = noop,
    onRowSelectionChange = noop,
    onClickFirstPage = noop,
    onClickLastPage = noop
  } = props;

  const table = useReactTable({
    data,
    columns,
    state: { sorting, rowSelection },
    getCoreRowModel: getCoreRowModel(),
    onSortingChange,
    enableSorting,
    onRowSelectionChange,
    getSortedRowModel: getSortedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    getPaginationRowModel: getPaginationRowModel(),
    debugTable: DEV,
    manualSorting: true,
    enableRowSelection,
    getRowId: (row) => row[uniqueKey] as string
  });

  const shouldRenderPagination = useMemo(
    () => enablePagination && !isLoading && pageMetadata.total !== 0,
    [enablePagination, isLoading, pageMetadata.total]
  );

  return (
    <div className="flex flex-col w-full">
      <table className="qb-table">
        <thead className="qb-table__head">
          {table.getHeaderGroups().map((headerGroup) => (
            <tr key={headerGroup.id} className="qb-table__head-tr">
              {headerGroup.headers.map((header) => {
                const hasSorting = header.column.getCanSort();
                const CustomTag = hasSorting ? 'button' : 'div';

                return (
                  <th
                    key={header.id}
                    colSpan={header.colSpan}
                    className="qb-table__th"
                    style={{ width: header.getSize() }}
                  >
                    <CustomTag
                      {...(hasSorting && {
                        type: 'button',
                        onClick: header.column.getToggleSortingHandler()
                      })}
                      className={classNames('qb-table__column', {
                        'qb-table__column--sort': header.column.getCanSort()
                      })}
                    >
                      {header.isPlaceholder ? null : (
                        <Typography size="sm" color="text-gray-800">
                          {flexRender(header.column.columnDef.header, header.getContext())}
                        </Typography>
                      )}
                      {{
                        asc: <Icon name="icon-chevron-up-small" className="qb-table__sort-icon" />,
                        desc: (
                          <Icon name="icon-chevron-down-small" className="qb-table__sort-icon" />
                        )
                      }[header.column.getIsSorted() as string] ?? null}
                    </CustomTag>
                  </th>
                );
              })}
            </tr>
          ))}
        </thead>
        <tbody className="qb-table__body">
          {isLoading ? (
            <tr className="qb-table__body-tr">
              <td colSpan={columns.length} className="qb-table__td">
                LOADING...
              </td>
            </tr>
          ) : (
            <>
              {data.length > 0 ? (
                <>
                  {table.getRowModel().rows.map((row) => (
                    <tr
                      key={row.id}
                      className={classNames('qb-table__body-tr', {
                        'qb-table__body-tr--selected': row.getIsSelected()
                      })}
                    >
                      {row.getVisibleCells().map((cell) => (
                        <td key={cell.id} className="qb-table__td">
                          <Typography size="sm">
                            {flexRender(cell.column.columnDef.cell, cell.getContext())}
                          </Typography>
                        </td>
                      ))}
                    </tr>
                  ))}
                </>
              ) : (
                <tr className="qb-table__body-tr">
                  <td
                    colSpan={table.getAllColumns().length}
                    className="qb-table__td qb-table__td--empty"
                  >
                    <EmptyView />
                  </td>
                </tr>
              )}
            </>
          )}
        </tbody>
      </table>
      <div className="flex items-end justify-between">
        {shouldRenderPagination && (
          <>
            <div className="qb-pagination">
              <button
                type="button"
                className={classNames('qb-pagination__arrow-button', {
                  'qb-pagination__arrow-button--disabled': pageMetadata.currentPage === 1
                })}
                onClick={() => {
                  onClickFirstPage({ selected: 0 });
                }}
              >
                <Icon
                  name="icon-chevron-double-left"
                  className={classNames('qb-pagination__arrow-icon', {
                    'qb-pagination__arrow-icon--disabled': pageMetadata.currentPage === 1
                  })}
                />
              </button>
              <ReactPaginate
                initialPage={Math.max(pageMetadata.currentPage - 1, 0)}
                {...initialPaginationSettings}
                nextLabel={
                  <Icon name="icon-chevron-right-small" className="qb-pagination__arrow-icon" />
                }
                previousLabel={
                  <Icon name="icon-chevron-left-small" className="qb-pagination__arrow-icon" />
                }
                {...paginationSettings}
                onPageChange={onPageChange}
                pageCount={Math.ceil(pageMetadata.lastPage)}
                renderOnZeroPageCount={null}
              />
              <button
                type="button"
                className={classNames('qb-pagination__arrow-button', {
                  'qb-pagination__arrow-button--disabled':
                    pageMetadata.currentPage === pageMetadata.total
                })}
                onClick={() => {
                  onClickLastPage({ selected: pageMetadata.lastPage - 1 });
                }}
                disabled={pageMetadata.currentPage === pageMetadata.total}
              >
                <Icon
                  name="icon-chevron-double-right"
                  className={classNames('qb-pagination__arrow-icon', {
                    'qb-pagination__arrow-icon--disabled':
                      pageMetadata.currentPage === pageMetadata.lastPage
                  })}
                />
              </button>
            </div>
            <div>
              <Select
                variant="table"
                list={PagePerList}
                hasContext={false}
                name={'data-table-perpage'}
                defaultValue={table.getState().pagination.pageSize}
                onChange={(e) => {
                  onPerPageChange(e.value as number);
                  table.setPageSize(e.value as number);
                }}
              />
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default DataTable;
