import type { PropsWithChildren } from 'react';
import './index.scss';

import { AnimatePresence, motion } from 'framer-motion';

// ----------------------------------------------------------------------
export interface TabScreenProps extends PropsWithChildren {
  currentIndex?: number;
}

const TabScreen = ({ children, currentIndex }: TabScreenProps) => {
  return (
    <AnimatePresence>
      <motion.div
        key={currentIndex ? currentIndex : 'empty'}
        initial={{ y: 10, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        exit={{ y: -10, opacity: 0 }}
        transition={{ ease: [0.17, 0.67, 0.83, 0.67], duration: 0.2 }}
        className="tab-screen"
      >
        {children}
      </motion.div>
    </AnimatePresence>
  );
};

export default TabScreen;
