import type { ButtonHTMLAttributes, PropsWithChildren } from 'react';

import './index.scss';

// ----------------------------------------------------------------------

export type TouchableProps = PropsWithChildren & ButtonHTMLAttributes<HTMLButtonElement>;

const Touchable = ({ children, onClick }: TouchableProps) => {
  return (
    <button type="button" className="qb-touchable focus-visible--all" onClick={onClick}>
      {children}
    </button>
  );
};

export default Touchable;
