import './index.scss';

import { useCallback } from 'react';

import Typography from 'components/ui/Typography';

// ----------------------------------------------------------------------

export type LogoProps = {
  corporate?: string;
  hasImage?: boolean;
};

const Logo = ({ corporate = 'QUARTERBACK', hasImage = false }: LogoProps) => {
  const setLowercase = useCallback(() => {
    return corporate.toLowerCase();
  }, [corporate]);

  return (
    <h1 className="qb-logo">
      {hasImage ? (
        <img
          src={`/assets/images/ci/ci-${setLowercase()}.png`}
          alt="ci"
          className="qb-logo__image"
        />
      ) : (
        <Typography variant="head" size="sm" weight={500} className="qb-logo__label">
          {corporate}
        </Typography>
      )}
    </h1>
  );
};

export default Logo;
